@import "variables";

body{
  .content{
    .container{
      .content__howToMakeOrder {
        margin: 60px 0;
        display: flex;
        justify-content: space-between;
        div {
          width: 32.5%;
          height: 235px;
          display: flex;
          flex-wrap: wrap;
          background-color: #e6e6e6;
          border-radius: 5px;
          .icons {
            width: 40%;
            display: flex;
            justify-content: flex-end;
            .arrow {
              margin: 30px 0 0 0;
              width: 20%;
              font-size: 2em;
              color: #1b6b77;
              text-align: right;
            }
            .cart,
            .box,
            .truck {
              margin: 40px 0 0;
              width: 100px;
              height: 100px;
              display: flex;
              justify-content: center;
              align-items: center;
              font-size: 3.5em;
              color: #1b6b77;
              border: 2px solid #1b6b77;
              border-radius: 75px;
            }
          }
          .learnMore {
            width: 60%;
            display: flex;
            justify-content: center;
            align-items: center;
            a {
              width: 165px;
              height: 45px;
              font: .875em/45px @sansregular;
              color: #bababa;
              text-align: center;
              text-decoration: none;
              border: 1px solid #bababa;
              border-radius: 5px;
              &:hover {
                color: #fff;
                background-color: #1b6b77;
                transition: .5s;
              }
            }
          }
          .info {
            margin-left: 110px;
            width: 100%;
            font: 1em/1.125em @sansregular;
            color: #474747;
            span:nth-child(1) {
              font: 1.125em/1.25em @sansbold;
            }
          }
        }
      }
    }
  }
}