body .content .container .content__howToMakeOrder {
  margin: 60px 0;
  display: flex;
  justify-content: space-between;
}
body .content .container .content__howToMakeOrder div {
  width: 32.5%;
  height: 235px;
  display: flex;
  flex-wrap: wrap;
  background-color: #e6e6e6;
  border-radius: 5px;
}
body .content .container .content__howToMakeOrder div .icons {
  width: 40%;
  display: flex;
  justify-content: flex-end;
}
body .content .container .content__howToMakeOrder div .icons .arrow {
  margin: 30px 0 0 0;
  width: 20%;
  font-size: 2em;
  color: #1b6b77;
  text-align: right;
}
body .content .container .content__howToMakeOrder div .icons .cart,
body .content .container .content__howToMakeOrder div .icons .box,
body .content .container .content__howToMakeOrder div .icons .truck {
  margin: 40px 0 0;
  width: 100px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3.5em;
  color: #1b6b77;
  border: 2px solid #1b6b77;
  border-radius: 75px;
}
body .content .container .content__howToMakeOrder div .learnMore {
  width: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
}
body .content .container .content__howToMakeOrder div .learnMore a {
  width: 165px;
  height: 45px;
  font: 0.875em/45px "PTSans-Regular";
  color: #bababa;
  text-align: center;
  text-decoration: none;
  border: 1px solid #bababa;
  border-radius: 5px;
}
body .content .container .content__howToMakeOrder div .learnMore a:hover {
  color: #fff;
  background-color: #1b6b77;
  transition: .5s;
}
body .content .container .content__howToMakeOrder div .info {
  margin-left: 110px;
  width: 100%;
  font: 1em/1.125em "PTSans-Regular";
  color: #474747;
}
body .content .container .content__howToMakeOrder div .info span:nth-child(1) {
  font: 1.125em/1.25em "PTSans-Bold";
}
